import React, { Component } from "react";
import ReactDOM from "react-dom";

import * as THREE from "three";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

import './Scene.css';
import texture from '../imgs/resume.jpg';


class Scene extends Component {
    componentDidMount() {
        var scene = new THREE.Scene();
        var camera = new THREE.PerspectiveCamera( 75, window.innerWidth/window.innerHeight, 0.1, 1000 );
        var renderer = new THREE.WebGLRenderer( {alpha: true, antialias:true} );
        renderer.setSize( window.innerWidth-20, window.innerHeight-20 );
        // renderer.setClearColor( 0x000000, 0 );

        this.mount.appendChild( renderer.domElement );
      
        var geometry = new THREE.BoxGeometry( 1, 1, 1 );
        // const geometry = new THREE.IcosahedronGeometry(1, 0);
        const material = new THREE.MeshPhysicalMaterial({
          roughness: 0,
          // transmission: 1,
          thickness:0.5,
        });        
        const mesh = new THREE.Mesh(geometry, material)
        scene.add(mesh);

const controls = new OrbitControls( camera, renderer.domElement );


        const light = new THREE.DirectionalLight(0xfff0dd, 1);
        light.position.set(0, 5, 10);
        scene.add(light);

        // const bgTexture = new THREE.VideoTexture( "http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4" )
        // const bgTexture = new THREE.TextureLoader().load("https://cdn.glitch.com/62a3a7d1-3c19-4fb7-b1ef-a1c65ba38596%2Fboard.svg?v=1577426114562");
        // const bgGeometry = new THREE.PlaneGeometry(5, 5);
        // const bgMaterial = new THREE.MeshBasicMaterial({ map: bgTexture });
        // const bgMesh = new THREE.Mesh(bgGeometry, bgMaterial);
        // bgMesh.position.set(0, 0, -1);
        // scene.add(bgMesh);


camera.position.z = 2;
controls.update();
var animate = function () {
            requestAnimationFrame( animate );
            mesh.rotation.x += 0.01;
            mesh.rotation.y += 0.01;
            mesh.rotation.z += 0.01;
            renderer.render( scene, camera );
        };
        animate();
        controls.update();
    }
    render() {
        return (
            <div id="scene" ref={ref => (this.mount = ref)} />
        )
    }
}

export default Scene;
