import React, { Component } from "react";
import ReactDOM from "react-dom";
import * as THREE from "three";
import { WebGLRenderer } from "three";

// import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';
import { EXRLoader } from 'three/examples/jsm/loaders/EXRLoader.js';
// post processing imports
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { HalftonePass } from 'three/examples/jsm/postprocessing/HalftonePass.js';
import { FilmPass } from 'three/examples/jsm/postprocessing/FilmPass.js';
import { BloomPass } from 'three/examples/jsm/postprocessing/BloomPass.js';
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
import { GammaCorrectionShader } from 'three/examples/jsm/shaders/GammaCorrectionShader.js';
import { HueSaturationShader } from 'three/examples/jsm/shaders/HueSaturationShader.js';
import { BrightnessContrastShader } from 'three/examples/jsm/shaders/BrightnessContrastShader.js';


import model from '../models/eye model modified mountains.glb';
import hdri from '../hdris/rooitou_park_4k.exr';
import './Scene.css';

class SceneTest extends Component {
    
    componentDidMount() {
        // setting up event listeners: window resize & camera moves with mouse
        let camera, scene, renderer, composer;
        let mouseX = 0;
        let mouseY = 0;
        let windowHalfX = window.innerWidth / 2;
        let windowHalfY = window.innerHeight / 2;

        let defaultYPos = 5;

        init();
        this.mount.appendChild( renderer.domElement );
        window.addEventListener( 'resize', onWindowResize );
        document.addEventListener( 'mousemove', onDocumentMouseMove );
        // animate();

        // setting up the scene 
        function init() {

            scene = new THREE.Scene();
            camera = new THREE.PerspectiveCamera(30, window.innerWidth/window.innerHeight, 1, 1000 );
            // const light = new THREE.DirectionalLight(0xfff0dd, .1);
            // light.position.set(0, 5, 10);
            // scene.add(light);

            // setting up background texture
            const backgroundTexture = new THREE.TextureLoader().load(require('../imgs/world.jpg'));
            backgroundTexture.mapping = THREE.EquirectangularRefractionMapping;
            backgroundTexture.encoding = THREE.sRGBEncoding;
            scene.background = backgroundTexture;

            // setting up hdri
            new EXRLoader().load( hdri, function ( texture ) {
                animate();

                texture.mapping = THREE.EquirectangularReflectionMapping;
                scene.environment = texture;

                // loading blender model through gltf
                const loader = new GLTFLoader();
                // using dracoLoader to compress, need to find relative path replacement for url in decoder path
                const dracoLoader = new DRACOLoader();
                dracoLoader.setDecoderPath( 'https://www.gstatic.com/draco/versioned/decoders/1.4.3/' ); 
                loader.setDRACOLoader( dracoLoader );
                
                loader.load( model, function ( gltf ) {
                    scene.add( gltf.scene );
                    console.log("pre camera positioning y=" + camera.position.y + "\n");
                    camera.position.copy(gltf.cameras[0].parent.position);
                    // camera.position.y += 30;
                    camera.position.z -= 5;
                    console.log("post camera positioning y=" + camera.position.y + "\n");
                },  
                // called while loading is progressing
                function ( xhr ) {
                    console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );

                },
                // called when loading has errors
                function ( error ) {
                console.log( 'An error happened' );
                } );
            
            });

            // adding and configuring renderer
            // renderer = new THREE.WebGLRenderer( {alpha: true, antialias:true} );
            renderer = new WebGLRenderer({
                powerPreference: "high-performance",
                antialias: false,
                stencil: false,
                depth: false
            });

            renderer.setSize( window.innerWidth, window.innerHeight );
            renderer.setPixelRatio( window.devicePixelRatio );
            renderer.outputEncoding = THREE.sRGBEncoding; // setting colour space
            renderer.autoClear = false;
            renderer.toneMapping = THREE.CineonToneMapping;
            renderer.toneMappingExposure = 1.3;

            addPostProcessing();

        }

        function addPostProcessing(){
            composer = new EffectComposer( renderer );
            const renderPass = new RenderPass( scene, camera );
            composer.addPass( renderPass );


            const params = {
                    shape: 1,
                    radius: 5,
                    rotateR: Math.PI / 12,
                    rotateB: Math.PI / 12 * 2,
                    rotateG: Math.PI / 12 * 3,
                    scatter: .25,
                    blending: .75,
                    blendingMode: 1,
                    greyscale: false,
                    disable: false
                };

            const halftonePass = new HalftonePass( window.innerWidth, window.innerHeight, params );
            composer.addPass( halftonePass );

            const bloomPass = new BloomPass(1,10, 3, 256);
            composer.addPass(bloomPass);
            
            const filmPass = new FilmPass( 0.7, 0.05, 2048, 0 );
            composer.addPass(filmPass);

            const brightnessContrastPass = new ShaderPass( BrightnessContrastShader );
            brightnessContrastPass.uniforms[ "contrast" ].value = -0.05;
            // brightnessContrastPass.uniforms[ "brightness" ].value = -0.2;
            composer.addPass(brightnessContrastPass);

            const hueSaturationPass = new ShaderPass( HueSaturationShader);
            hueSaturationPass.uniforms[ "hue" ].value = 0.05;
            hueSaturationPass.uniforms[ "saturation" ].value = 0.2;
            composer.addPass(hueSaturationPass);

            const gammaCorrectionPass = new ShaderPass( GammaCorrectionShader );
            composer.addPass(gammaCorrectionPass);

        }

        function onWindowResize() {
            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();
            renderer.setSize( window.innerWidth, window.innerHeight );
            composer.setSize( window.innerWidth, window.innerHeight );

        }

        function onDocumentMouseMove( event ) {
            mouseX = ( event.clientX - windowHalfX ) / 100;
            mouseY = ( event.clientY - windowHalfY ) / 100;
        }

        // adding camera movement on mouse move
        var animate = function () {
            requestAnimationFrame( animate );
            render();
        };

        function render() {
            // * number inside is magnitude, the bigger the number the smaller the magnitude
            // * number outside is speed
            // camera.position.x += ( mouseX - camera.position.x*2) * .1; 
            console.log("pre render camera y=" + camera.position.y + "\n");

            console.log(mouseY);
            let yOffset = mouseY*.25 - 1;
            let xOffset = mouseX*.25 - 1;

            camera.position.y = (camera.position.y + ( (yOffset) - camera.position.y)) + defaultYPos ; 
            camera.position.x = (camera.position.x + ( (xOffset) - camera.position.x)); 

            // camera.position.y += (mouseY * 0.1 - camera.position.y*0.1); 

            console.log("render mouseY=" + mouseY + "\n");
            console.log("render camera y=" + camera.position.y + "\n");






            // camera.position.y += ( mouseY/10 - camera.position.y) * .1; 
            camera.lookAt( scene.position );

// camera..y = THREE.MathUtils.lerp(camera..y, (mouseX * Math.PI) / 10, 0.1)
// camera..x = THREE.MathUtils.lerp(camera..x, (mouseX * Math.PI) / 10, 0.1)

            // renderer.render( scene, camera );
           
            composer.render();
        }
    }

    render() { return (<div id="scene" ref={ref => (this.mount = ref)} />)}
}

export default SceneTest;

