import clouds from '../videos/clouds.mp4'
import coolclouds from '../videos/cool clouds.mov'
import jellyfish from '../videos/jellyfish.mp4'

import React from "react";

function BackgroundVideo() {
  return (
      <video loop autoPlay muted 
      class="fixed h-screen w-screen object-cover -z-1 inset-0
      filter sepia-bgVid saturate-bgVid brightness-bgVid hue-rotate-bgVid
      ">
        <source src={coolclouds} type="video/mp4"/>
      </video>


  );
}


export default BackgroundVideo;


